</template>
<script>
import rightStickIcon from "../../components/rightStickIcon";
import axios from "axios";
export default {
  components: {
    rightStickIcon,
  },

  data() {
    return {
      headersone: [
        {
          text: "Levels",
          align: "center",
          sortable: false,
          value: "level",
        },

        {
          text: "Space Availability",
          align: "center",
          value: "spaceAvailability",
        },
      ],
      westparkings: [
        // {
        //   level: "Total",
        //   spaceAvailability: 7,
        // },
        // {
        //   level: "Ground",
        //   spaceAvailability: 1,
        // },
        // {
        //   level: "Level 1",
        //   spaceAvailability: 0,
        // },
        // {
        //   level: "Level 2",
        //   spaceAvailability: 0,
        // },
        // {
        //   level: "Level 3",
        //   spaceAvailability: 1,
        // },
        // {
        //   level: "Level 4",
        //   spaceAvailability: 5,
        // },
        // {
        //   level: "Level 4",
        //   spaceAvailability: 0,
        // },
        // {
        //   level: "Roof",
        //   spaceAvailability: 396,
        // },
      ],

      headerstwo: [
        {
          text: "Levels",
          align: "center",
          sortable: false,
          value: "level",
        },

        {
          text: "Space Availability",
          align: "center",
          value: "spaceAvailability",
        },
      ],
      eastparkings: [
        // {
        //   level: "Total",
        //   spaceAvailability: 563,
        // },
        // {
        //   level: "Ground",
        //   spaceAvailability: 19,
        // },
        // {
        //   level: "Level 1",
        //   spaceAvailability: 51,
        // },
        // {
        //   level: "Level 2",
        //   spaceAvailability: 16,
        // },
        // {
        //   level: "Level 3",
        //   spaceAvailability: 145,
        // },
        // {
        //   level: "Level 4",
        //   spaceAvailability: 143,
        // },
        // {
        //   level: "Level 4",
        //   spaceAvailability: 189,
        // },
        // {
        //   level: "Roof",
        //   spaceAvailability: 233,
        // },
      ],
      istTime: "",
    };
  },
  mounted() {
    this.getParkingDetail();
    this.DisplayCurrentTime();
  },
  methods: {
    async getParkingDetail() {
      let westparkings = await axios.get(`${process.env.VUE_APP_PARKING_URL}2`);

      westparkings.data.forEach((element) => {
        let obj = {
          level: element.Countername,
          spaceAvailability: element.EmptyCar,
        };
        this.westparkings.push(obj);
      });
      let eastparkings = await axios.get(`${process.env.VUE_APP_PARKING_URL}1`);
      eastparkings.data.forEach((element) => {
        let obj = {
          level: element.Countername,
          spaceAvailability: element.EmptyCar,
        };
        this.eastparkings.push(obj);
      });
    },
    DisplayCurrentTime() {
      let date = new Date();
      console.log(
        date.getDate() + ":" + date.getMonth() + ":" + date.getFullYear()
      );

      let hours = date.getHours() > 12 ? date.getHours() - 12 : date.getHours();

      console.log("date.getHours()" + date.getHours());
      let am_pm = date.getHours() >= 12 ? "PM" : "AM";
      hours = hours < 10 ? "0" + hours : hours;
      let minutes =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      let month =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      console.log("month " + month);
      this.istTime =
        date.getDate() +
        ":" +
        month +
        ":" +
        date.getFullYear() +
        " " +
        date.getHours() +
        ":" +
        minutes;

      console.log("this.istTime" + this.istTime);
    },
  },

  computed: {},
};
</script>
<style scoped>
.mlcptext {
  width: 90%;
}

.tableHead {
  text-align: center;
  background: #f9fafb;
  border-radius: 35px 35px 0 0;
  padding: 15px 0;
  width: 99%;
  margin: 0 auto;
  border: thin solid rgba(0, 0, 0, 0.12);
}

.tableHead p {
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  text-align: center;
  letter-spacing: -0.02em;
  color: #374151;
}

.tableHead span {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #6b7280;
}

.theme--light.v-data-table
  > .v-data-table__wrapper
  > table
  > thead
  > tr:last-child
  > th {
